.cart-item-number {
  background: #ada2ff;
}

.new-color-title {
  color: #ada2ff;
}

.new-border-color {
  border-color: #ada2ff;
}

.bump {
  animation: bump 300ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }

  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
